.store{display: grid;}
.store-header{
  text-align: right;
  margin-right: 20px;
  padding: 10px;
}
/* .material-icons{position: absolute; right: 21px;} */
.material-icons:hover{cursor: pointer;}

                /* <=== ShopCard ===> */
.cards-view{
  display: grid; grid-template-columns: repeat(3, 4fr); 
  grid-template-rows: auto; grid-gap: 11px;
}
.shop-card{
  width: 260px; height: 260px;
  padding: 31px; border-radius: 5px; 
  background-color: #f5f5f6; text-align: center;
  position: relative;
  margin-bottom: 28px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.card-name{
  text-transform: uppercase;
  z-index: 3; 
  font-size: 21px;
  position: absolute;
  top: 7%;
  left: 0;
  right: 0;
}
.card-color {
  position: absolute;
  z-index: 3;
  top: 20%;
  left: 0;
  right: 0;
  font-size: 14px;
}
.card-price {
  position: absolute;
  z-index: 3;
  bottom: 33px;
  left: 38px;
  font-size: 16px;
  color: #D77074;
}
button.add-to-cart {
  position: absolute;
  z-index: 3;
  bottom: 25px;
  background-color: #f5f5f6;
  color: #D77074;
  border: 1px solid #D77074;
  border-radius: 10em;
  font-weight: bold;
  padding: 5px 15px;
  height: 35px;
}
button.add-to-cart:hover{background-color: #457;}
.card-img{
  width: 300px; 
  height: auto;
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  left: 0;
}

                /* <=== ShopItem ===> */
.list-view {
  display: grid;
  grid-template-rows: auto;
  grid-template-columns: 1fr;
  width: 96%;
}
.shop-item {
  display: grid;
  grid-template-columns: repeat(5, 2fr);
  text-align: center;
  align-items: center;
  background-color: #F5F5F5;
  padding: 15px 15px 5px;
  overflow: hidden;
  margin-bottom: 10px;
}
img.item-img{width: 137px; margin-top: -50px;}
.item-name{text-transform: uppercase; font-size: 21px;}
.item-color{font-size: 14px;}
.item-price{font-size: 16px; color: #D77074;}
.item-to-cart{
  background-color: #f5f5f6;
  color: #D77074;
  border: 1px solid #D77074;
  border-radius: 10em;
  font-weight: bold;
  padding: 5px 15px;
  width: fit-content;
  height: 35px;
}
button.item-to-cart:hover{background-color: #457 !important;}

/* HomeWork 2/2 ---- END */


